* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
a,
a:visited {
	text-decoration: none;
	color: #426696;
}

button {
	border: none;
	color: #426696;
	font-weight: 600;
	background: none;
	font-size: 100%;
	outline: none;
}

h1 {
	color: #426696;
	font-weight: 600;
	font-size: 3rem;
	opacity: 0.8;
}

h2,
p {
	color: #658ec6;
	font-weight: 600;
	opacity: 0.9;
}

h3 {
	color: #426696;
	font-weight: 600;
	opacity: 0.8;
}

main {
	font-family: "Poppins", sans-serif;
	min-height: 100vh;
	min-width: 100vh;
	background: linear-gradient(to right top, #65dfc5, #5d9ce4);
	display: flex;
	align-items: center;
	justify-content: center;
}

/* .about p {
	font-weight: normal;
	font-size: 15pt;
} */

.blue {
	background: linear-gradient(to right top, #65dfc5, #5d9ce4);
}

.proBlue {
	background: linear-gradient(to right top, #65dfc5, #5d9ce4);
	border-radius: 2rem;
	color: white;
	padding: 15%;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	margin-left: 15px;
	margin-right: 15px;
	margin-bottom: 15px;
}

.proBlue h2 {
	color: white;
	font-size: 110%;
	font-weight: 600;
	width: 100%;
	display: flex;
	justify-content: flex-end;
}

.red {
	background: linear-gradient(to right top, #ffc0a2fd, #f50505);
}
.proRed {
	background: linear-gradient(to right top, #ffc0a2fd, #f50505);
	border-radius: 2rem;
	color: white;
	padding: 15%;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	margin-left: 15px;
	margin-right: 15px;
	margin-bottom: 15px;
}
.proRed h2 {
	color: white;
	font-size: 110%;
	font-weight: 600;
	width: 100%;
	display: flex;
	justify-content: flex-end;
}

.green {
	background: linear-gradient(to right top, #7af36f, #01a029);
}
.proGreen {
	background: linear-gradient(to right top, #7af36f, #01a029);
	border-radius: 2rem;
	color: white;
	padding: 15%;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	margin-left: 15px;
	margin-right: 15px;
	margin-bottom: 15px;
}
.proGreen h2 {
	color: white;
	font-size: 110%;
	font-weight: 600;
	width: 100%;
	display: flex;
	justify-content: flex-end;
}

.pink {
	background: linear-gradient(to right top, #f393f7fd, #8a41eb);
}
.proPink {
	background: linear-gradient(to right top, #f393f7fd, #8a41eb);
	border-radius: 2rem;
	color: white;
	padding: 15%;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
	margin-left: 15px;
	margin-right: 15px;
	margin-bottom: 15px;
}
.proPink h2 {
	color: white;
	font-size: 110%;
	font-weight: 600;
	width: 100%;
	display: flex;
	justify-content: flex-end;
}

.glass {
	background: white;
	min-height: 50vh;
	max-height: 80vh;
	width: 80%;
	margin-top: 20px;
	margin-bottom: 20px;
	background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4));
	border-radius: 2rem;
	z-index: 2;
	backdrop-filter: blur(2rem);
	display: flex;
}

.dashboard {
	min-height: 50vh;
	max-height: 80vh;
	min-width: 30vh;
	max-width: 40vh;
	flex: 0.5;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	text-align: center;
	font-size: 2.2vh;
	background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.3));
	border-radius: 2rem;
}

.about {
	width: 60%;
	height: 0.8fr;
	padding: 2%;
	display: flex;
	flex-direction: column;
	align-items: left;
	justify-content: space-evenly;
	text-align: left;
	flex: 1;
}

.projects {
	width: 60%;
	height: 100%;
	padding: 2%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	flex: 1;
}

.projects h1 {
	text-align: center;
}

.contact {
	width: 60%;
	height: 100%;
	padding: 2%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	text-align: center;
	flex: 1;
}

.circle1 {
	background: white;
	background: linear-gradient(to right top, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
	height: 25rem;
	width: 25rem;
	position: absolute;
	border-radius: 50%;
}

.circle2 {
	background: white;
	background: linear-gradient(to left bottom, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
	height: 20rem;
	width: 20rem;
	position: absolute;
	border-radius: 50%;
}

.circle1 {
	top: 5%;
	right: 3%;
}
.circle2 {
	bottom: 5%;
	left: 5%;
}

img {
	width: 50px;
	height: 50px;
	object-fit: cover;
}

.cards img {
	width: 6rem;
	height: 6rem;
}

.card {
	margin-bottom: 3%;
}

.card h2 {
	text-align: center;
}

.project-info {
	display: grid;
	grid-template-columns: 150px 3fr;
}

.project-info .description {
	margin-left: 10px;
}

.project-info .proj1-img,
.project-info .proj2-img,
.project-info .proj3-img,
.project-info .proj4-img,
.project-info .proj5-img,
.project-info .proj6-img {
	height: 150px;
	width: 150px;
	border-radius: 2rem;
}

.link {
	display: flex;
	align-items: center;
	height: 60px;
	margin-left: 10px;
	margin-bottom: 5px;
}

.projectBtn,
.aboutBtn,
.contactBtn {
	cursor: pointer;
}

.pictureOfMe {
	width: 6rem;
	height: 6rem;
	margin-top: 1rem;
	border-radius: 50%;
}

.link h2,
.linkProjects h2,
.linkAboutMe h2,
.linkLinkedIn h2,
.linkGithub h2,
.linkContactMe h2 {
	padding: 0rem 1rem;
	align-items: center;
}

/* .projects {
	flex: 2;
} */

.theme-dot {
	height: 30px;
	width: 30px;
	border-radius: 50%;
	border: 2px solid white;
}

#blue-mode {
	background: linear-gradient(to right top, #65dfc9, #5d9ce4);
	cursor: pointer;
}

#pink-mode {
	background: linear-gradient(to right top, #f393f7fd, #8a41eb);
	cursor: pointer;
}

#red-mode {
	background: linear-gradient(to right top, #ffc0a2fd, #f50505);
	cursor: pointer;
}

#green-mode {
	background: linear-gradient(to right top, #7af36f, #01a029);
	cursor: pointer;
}

#theme-options-wrapper {
	display: flex;
	justify-content: center;
}

.pro {
	background: linear-gradient(to right top, #65dfc9, #5d9ce4);
	border-radius: 2rem;
	color: white;
	margin-left: 15px;
	margin-right: 15px;
	margin-bottom: 15px;
}

.status {
	margin-bottom: 1rem;
}

.status input {
	background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.3));
	border: none;
	width: 50%;
	padding: 0.5rem;
	border-radius: 2rem;
	outline: none;
}

/* .projects {
	margin: 1rem;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
} */

.about h1 {
	font-size: 250%;
	margin: 0rem;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}

.scrollbar p {
	font-size: 100%;
}

.scrollbar {
	height: 60vh;
	overflow: hidden;
	overflow-y: scroll;
	font-size: 1.1rem;
}

.scrollbarProject {
	height: 25rem;
	overflow: hidden;
	overflow-y: scroll;
}
@media only screen and (max-width: 774px) {
	.scrollbarProject {
		height: 25rem;
	}
}
@media only screen and (min-width: 1000px) and (max-width: 1300px) {
	.scrollbarProject {
		height: 25rem;
	}
}

#contact-form {
	display: block;
	max-width: 600px;
	margin: 0 auto;
	border: 1px solid;
	padding: 15px;
	border-radius: 5px;
	margin-bottom: 5px;
}

#contact-form label {
	line-height: 2.7em;
}

#contact-form textarea {
	min-height: 100px;
	font-size: 14px;
}

.input-field {
	font-family: "Poppins", sans-serif;
	width: 100%;
	padding-top: 0px;
	padding-bottom: 0px;
	border-radius: px;
	border: 1px solid;
	font-size: 14px;
	background-color: white;
	background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.3));
}

#submit-btn {
	margin-top: 10px;
	width: 100%;
	padding-top: 10px;
	padding-bottom: 10px;
	cursor: pointer;

	background-color: white;
	background: linear-gradient(to right bottom, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.3));
	border: none;
}

